body {
    margin: 0;
    padding: 0;
    background: #f2f4f5;
    color: #1e1e1f;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
}

#root {
    margin: 0 24px;
    position: relative;
    min-height: 100vh;
}

.TodoCounter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    margin-bottom: -30px;
    padding: 48px;
}

.icon-image {
    display: flex;
    justify-content: center;
    margin: auto;
    margin-bottom: -30px;
}

.TodoSearch {
    background: #F9FBFC;
    border-radius: 10px;
    border: 1px solid #c8c8c8;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-color: #61DAFA;
  }
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
  }
.TodoItem {
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.Icon-check {
    position: absolute;
    left: 12px;
}
.Icon-check--active {
    color: #4caf50;
}

.Icon-delete {
    position: absolute;
    /* top: -24px; */
    right: 0;
}
.Icon-delete:hover {
    color: red;
}

.check,
.delete {
    height: 20px;
}

.check-active {
    background-color: #4caf50;
}

.delete:hover {
    background-color: red;
}

form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1e1e1f;
    margin-bottom: 26px;
}

textarea {
    background-color: #f9fbfc;
    border: 1px solid #c8c8c8;
    border-radius: 10px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1e1e1f;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    font-family: "Montserrat";
    resize: vertical;
    max-height: 400px;
}

textarea::-webkit-input-placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

textarea::placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

textarea:focus {
    outline-color: #61dafa;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 10px;
    border: none;
    font-family: "Montserrat";
}

.TodoForm-button--add:hover {
    background: #36cbf8;
}

.TodoForm-button--add {
    background: #61dafa;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--cancel {
    background: transparent;
}
.TodoForm-button--cancel:hover {
    background: #cccccc;
}

.msg-text {
    color: #202329;
    font-size: 14px;
}

.CreateTodoButton {
    background-color: #61dafa;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    -webkit-transform: rotate(0);

            transform: rotate(0);
    transition: 0.3s ease;

    z-index: 1;
}

.CreateTodoButton:hover {
    box-shadow: 0px 5px 25px rgb(0 0 0 / 50%);
    background-color: #159bbe;
}

.CreateTodoButtonClose {
    background-color: #159bbe;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    -webkit-transform: rotate(224deg);

            transform: rotate(224deg);
    transition: 0.3s ease;

    z-index: 1;
}

.CreateTodoButtonClose:hover {
    background-color: #61dafa;
}

.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.loading-image {
    display: flex;
    justify-content: center;
}

.center-wrapper {
    width: 50%;
    margin: auto;
    min-width: 270px;
}

